/* Import monospace font */
@import url('https://fonts.googleapis.com/css2?family=Fira+Code:wght@400;500;600&display=swap');

/* Terminal color palette */
:root {
  --bg-color: #0d1117;
  --text-color: #c9d1d9;
  --prompt-green: #56d364;
  --prompt-blue: #79c0ff;
  --prompt-yellow: #e3b341;
  --highlight-blue: #58a6ff;
  --link-color: #58a6ff;
  --link-hover: #79c0ff;
  --border-color: #30363d;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: 'Fira Code', 'Menlo', 'Monaco', 'Courier New', monospace;
  background-color: var(--bg-color);
  color: var(--text-color);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.home {
  min-height: 100vh;
  background-color: var(--bg-color);
  color: var(--text-color);
  font-family: 'Fira Code', 'Menlo', 'Monaco', 'Courier New', monospace;
}

/* Header with shell prompt */
.header {
  padding: 2rem 1rem;
  border-bottom: 1px solid var(--border-color);
}

.header-prompt {
  max-width: 900px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 0.5rem;
  font-size: 0.95rem;
  font-family: 'Fira Code', 'Menlo', 'Monaco', 'Courier New', monospace;
}

.prompt-symbol {
  color: var(--prompt-green);
}

.prompt-path {
  color: var(--text-color);
}

.cursor {
  color: var(--prompt-green);
  animation: blink 1s infinite;
  margin-left: 2px;
}

@keyframes blink {
  0%, 50% { opacity: 1; }
  51%, 100% { opacity: 0; }
}

.nav-links {
  display: flex;
  gap: 0.5rem;
  margin-left: 1rem;
}

.nav-links a {
  color: var(--text-color);
  text-decoration: none;
  transition: color 0.2s ease;
}

.nav-links a:hover {
  color: var(--link-color);
}

.separator {
  color: var(--text-color);
  opacity: 0.5;
  margin: 0 0.5rem;
}

.social-icons {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.social-icons a {
  color: var(--text-color);
  display: inline-flex;
  align-items: center;
  transition: color 0.2s ease;
}

.social-icons a:hover {
  color: var(--link-color);
}

.social-icons svg {
  width: 16px;
  height: 16px;
}

/* Main content */
.main {
  max-width: 900px;
  margin: 0 auto;
  padding: 3rem 1rem;
}

.section {
  margin-bottom: 4rem;
  scroll-margin-top: 2rem;
}

.section-content {
  padding: 0;
}

/* Shell-style headings */
.shell-heading {
  font-size: 1.1rem;
  font-weight: 400;
  margin: 0 0 2rem;
  font-family: 'Fira Code', 'Menlo', 'Monaco', 'Courier New', monospace;
}

.prompt-green {
  color: var(--prompt-green);
}

.prompt-blue {
  color: var(--prompt-blue);
}

.prompt-text {
  color: var(--text-color);
}

/* About section */
.about-content {
  margin-top: 1rem;
}

.about-text {
  font-size: 0.95rem;
  line-height: 1.8;
}

.about-text p {
  margin: 0 0 1.5rem;
  color: var(--text-color);
}

.about-text p:last-child {
  margin-bottom: 0;
}

.highlight {
  color: var(--highlight-blue);
  text-decoration: underline;
  cursor: pointer;
}

.highlight:hover {
  color: var(--link-hover);
}

/* Experience section */
.experience-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.experience-list li {
  margin-bottom: 1rem;
  font-size: 0.95rem;
  line-height: 1.8;
}

.year {
  color: var(--prompt-yellow);
  font-weight: 500;
  margin-right: 0.5rem;
}

.experience-text {
  color: var(--text-color);
}

/* Projects section */
.projects-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.projects-list li {
  margin-bottom: 1.5rem;
  font-size: 0.95rem;
  line-height: 1.8;
}

.project-prefix {
  color: var(--prompt-blue);
}

.project-title {
  color: var(--text-color);
  font-weight: 500;
}

.project-description {
  color: var(--text-color);
  opacity: 0.9;
}

/* Skills section */
.skills-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.skills-list li {
  margin-bottom: 0.75rem;
  font-size: 0.95rem;
  line-height: 1.8;
}

.skill-category {
  color: var(--highlight-blue);
  font-weight: 500;
}

.skill-items {
  color: var(--text-color);
}

/* Links */
a {
  color: var(--link-color);
  text-decoration: none;
  transition: color 0.2s ease;
}

a:hover {
  color: var(--link-hover);
  text-decoration: underline;
}

/* Responsive design */
@media (max-width: 768px) {
  .header-prompt {
    font-size: 0.85rem;
    flex-direction: column;
    align-items: flex-start;
  }

  .nav-links {
    margin-left: 0;
    margin-top: 0.5rem;
  }

  .separator {
    display: none;
  }

  .social-icons {
    margin-top: 0.5rem;
  }

  .main {
    padding: 2rem 1rem;
  }

  .section {
    margin-bottom: 3rem;
  }

  .shell-heading {
    font-size: 1rem;
  }

  .about-text,
  .experience-list li,
  .projects-list li,
  .skills-list li {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .header-prompt {
    font-size: 0.8rem;
  }

  .shell-heading {
    font-size: 0.95rem;
  }

  .about-text,
  .experience-list li,
  .projects-list li,
  .skills-list li {
    font-size: 0.85rem;
  }
}
